}
.body .add-task-header i.active,
.body .task-header i.active {
  color: #4a90e2;
}
.body .add-task-header.star,
.body .task-header.star {
  background: #fff2dc;
}
.body .add-task-header .task-checkbox,
.body .task-header .task-checkbox {
  width: 24px;
  height: 24px;
  margin-right: 16px;
}
.body .add-task-header .task-title,
.body .task-header .task-title {
  width: 75%;
